.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 150px 100px;
  background: #171717;
}

.hero-content {
  width: 300px;
  padding-left: 80px;
  .contact-button {
    border-radius: 8px;
    border: none;
    padding: 12px 55px;
    min-width: 180px;
    background-color: #171717;
    color: #171717;
    box-shadow: inset -2px -74px 49px 8px #1391ac;

    border: none;
    font-size: 10px;
    font-weight: 900;
    cursor: pointer;
    outline: none;
    text-shadow: black;

    &:hover {
      box-shadow: 1px 1px 10px 1px #1391ac;
      color: #1391ac;
    }
  }
  .cv {
    box-shadow: inset -2px -74px 49px 8px #171717;
    background-color: #171717;
    border: 1px solid #1391ac;
    color: #1391ac;
    margin-top: 10px;
    &:hover {
      box-shadow: 1px 1px 10px 1px #1391ac;
      color: #1391ac;
    }
  }
}

.hero-title {
  font-size: 30px;
  margin-bottom: 10px;
  font-weight: 900;
  color: #777;
  position: relative;
  width: 300px;
  display: inline-block;
  padding-right: 20px;
  &::before {
    content: '';
    position: absolute;
    top: 25%;
    left: -60px;
    width: 35px;
    height: 1px;
    background-color: #1391ac;
    transform: translateY(-50%);
  }
}

.hero-subtitle {
  font-size: 13px;
  color: #777;
}

.hero-badge {
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  font-size: 0.9em;
  color: #777;
  font-weight: 900;
  img {
    width: 150px;
    height: auto;
    margin-bottom: 15px;
    border-radius: 15px;
  }
}

.scroll-and-icon {
  display: flex;
  justify-content: end;
  align-items: center;
  padding: 20px 50px;
  background-color: #171717;

  .socials {
    display: flex;
    gap: 20px;
    a {
      text-decoration: none;
      font-size: 0.5em;
      text-align: center;
      color: #777;
    }
    img {
      width: 50px;
      border-radius: 15px;
    }
    .icon {
      display: flex;
      flex-direction: column-reverse;
    }
  }
}

@media (max-width: 768px) {
  .hero {
    display: flex;
    justify-content: center;
    flex-direction: column;

    padding: 100px 20px;
    .hero-content {
      padding-left: 0px;
      display: flex;
      flex-direction: column;
      .contact-button {
        min-width: 230px;
        margin-bottom: 20px;
      }
    }
  }

  .hero-title {
    font-size: 30px;
    margin-bottom: 10px;
    font-weight: 900;
    color: #777;
    position: relative;
    display: inline-block;
    padding-right: 20px;
    &::before {
      top: 25%;
      left: -10px;
    }
  }

  .scroll-and-icon {
    justify-content: center;
  }
}
